   .subtotal {
       display: flex;
       flex-direction: column;
       justify-content: space-between;
       width: 300px;
       height: 200px;
       padding: 30px;
       background-color: #f3f3f3;
       border: 2px solid #dddddd;
       border-radius: 3px;  
       border-bottom: 5px solid #f3f3f3 ;
     }
    .subtotal_gift{
        display: flex;
        align-items: center;
    }
    .subtotal_gift > input {
        margin-right: 5px;
    }
    .subtotal > button {
        background: #f0c14b;
        border-radius: 2px;
        width: 100%;
        height: 30px;
        border: 1px solid;
        margin-top: 10px;
        border-color: #a88734 #9c7e31 #846a29;
        color: #111;
    }